import Index from "./index-model.js";
const index = new Index();
export default {
	data() {
		return {
			options: {},
			title: 'Hello',
			// imageUrl: '',
			authorizationButton: null,
			userInfo: {},
			userInfoAll: {},
			// 轮播图相关
			bannerData: [],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			// 客户经理数据对象
			managerNode: {
				data: []
			},
			// 最新资讯
			informationNode: {
				data: []
			}
		}
	},
	onLoad(options) {
		const that = this
		that.options = options
		that._onLoad()
	},
	onShow() {
		// 获取已授权类别
		const that = this
		uni.getSetting({
			success(res) {
				if (res.authSetting['scope.userInfo']) {
					// 隐藏授权按钮
					that.authorizationButton = false;
					that.$store.commit('updateAuthorizationButtonData', false);
					that.wx_login(() => {
						callBack && callBack();
					})
				}
			},
			fail() {
				console.log("获取授权信息授权失败")
			}
		})
		// let token = index.get_storage('token_type', callBack);
		// if(token) {
		// 	that.getUserInfo(() => {
		// 		that.getRunData(() => {
		// 			callBack && callBack();
		// 		})
		// 	})
		// }
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			// that.imageUrl = index.base_image_url
			that.userInfo = that.$store.state.userInfo;
			// 轮播图加载
			this.getBanner(() => {
				callBack && callBack();
			})
			// 客户经理列表加载
			this.getManagerList(() => {
				callBack && callBack();
			})
			// 客户经理列表加载
			this.getInformationList(() => {
				callBack && callBack();
			})
			// that.wx_login(() => {
			// 	that.getUserInfo(() => {
			// 		if (that.userInfo.type == 'member') {
						
			// 		} else {
			// 			// 提示用户非会员
			// 			that.promptOpenVip()
			// 		}
			// 	})
			// })
		},
		// 进入客户经理列表
		goManagerList() {
			index.navigate_to(`/pages/managerList/managerList`);
		},
		// 进入资讯列表
		goArticleList() {
			index.navigate_to(`/pages/articleList/articleList`);
		},
		// 进入--客户经理--详情页
		goManagerDetails(e) {
			const that = this;
			// const id = index.get_data_set(e, "id");
			const managerindex = index.get_data_set(e, "managerindex");
			let data = JSON.stringify(that.managerNode.data[managerindex]);
			index.navigate_to(`/pages/managerDetails/managerDetails?data=${data}`);
		},
		// 进入--资讯--详情页
		goInformationDetails(e) {
			const that = this;
			const id = index.get_data_set(e, "id");
			index.navigate_to(`/pages/informationDetails/informationDetails?id=${id}`);
		},
		wx_login(e) {
			const that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					var code = loginRes.code;
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							that.userInfoAll = infoRes
							that.$store.commit('updateUserInfo', that.userInfo);
							that.$store.commit('updateAuthorizationButtonData', false);
							index.login({
								code: code,
								role: '', // 角色
								portrait: infoRes.userInfo.avatarUrl,
								nickname: infoRes.userInfo.nickName
							}, (res) => {
								// console.log(res)
								if (res.code == 4000) {
									that.userInfo = res.data
									that.$store.commit('updateUserInfo', that.userInfo);
									that.$store.commit('updateAuthorizationButtonData', false);
									if (that.userInfo.role == "") {
										index.show_tips('检测到用户未选择权限类型，即将进入选择页')
										setTimeout(() => {
											index.navigate_to(`/pages/identitySel/identitySel`);
										}, 2000)
									}
									// that.$store.commit('updateUserInfo', res.data);
									// if (role == '1') {
									// 	index.switch_tab(`/pages/index/index`);
									// } else {
									// 	// validation => 0  未认证
									// 	// validation => 1  认证
									// 	// validation => 2  审核中
									// 	if (res.data.validation == 0) {
									// 		// 客户经理资质 已认证
									// 		index.switch_tab(`/pages/index/index`);
									// 	} else if (res.data.validation == 1) {
									// 		// 客户经理资质 未认证
									// 		index.navigate_to(`/pages/certification/certification`);
									// 	} else if (res.data.validation == 2) {
									// 		// 客户经理资质 审核中
									// 		index.show_tips('资质审核中')
									// 		return false
									// 	}
									// }
								}
							})
						}
					})
				}
			});
		},
		// 加载轮播图
		getBanner(callBack) {
			const that = this
			index.getBanner({}, (res) => {
				if (res.code == '4000') {
					that.bannerData = res.data
				}
				callBack && callBack();
			})
		},
		// 客户经理列表
		getManagerList(callBack) {
			const that = this
			index.getManagerList({}, (res) => {
				if (res.code == '4000') {
					that.managerNode = res
				}
				callBack && callBack();
			})
		},
		// 最新资讯
		getInformationList(callBack) {
			const that = this
			index.getInformationList({}, (res) => {
				if (res.code == '4000') {
					let list = res.data
					for (let i = 0; i < list.length; i++) {
						list[i].createtime = index.transformTime(list[i].createtime * 1000)
					}
					res.data = list
					that.informationNode = res
				}
				callBack && callBack();
			})
		},
		// 提示开通会员
		promptOpenVip(callBack) {
			uni.showModal({
				title: '系统提示',
				content: '系统检测到用户未开通会员，是否开通会员',
				success: res => {
					if (res.confirm) {
						index.navigate_to(`/pages/vip/vip`);
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		// let shareData = {
		// 	title: '',
		// 	path: `pages/index/index`,
		// 	imageUrl: ''
		// }
		return index.onShareAppMessage({});
	}
}
